/***/ (function(module, exports) {

	module.exports = {"data":{"post":{"html":"<p>It looks like the world went by without me for a week. Tel Aviv, as the French say...</p>\n<p><a href=\"http://brdbrain.edithispage.com/2000/07/07\">brdbrain</a>:  \"I need the six-foot-long poster of <a href=\"http://www.sfbayquakes.org/northview/north_without_USLett.jpg\">this image</a> of the Bay. You can get one with the fault lines on it, too! And there's lots of <a href=\"http://www.sfbayquakes.org/thumbnails.html\">views</a> to choose from.\" Looking at the graphic, I could point to the spot in San Jose where I lived until I was 12, over 25 years ago. <a href=\"http://maps.yahoo.com/py/maps.py?Pyt=Tmap&addr=Partridge+Dr&city=San+Jose&state=CA&slt=37.304800&sln=-121.781300&name=&zip=95121-1009&country=us&BFCat=&BFClient=&mag=9&desc=&cs=9&newmag=7\">Partridge Dr</a>. Strange how memory gets jolted.</p>\n<p>My mother-in-law Ruth also went back to her childhood home this week, for real, not just virtually.It was just the second time in 55 years. She grew up near Schwiebus (now Swiebodzin, Poland), about 50 km east of Frankfurt/Oder. Her parents farm was confiscated after the war and the entire family was moved west. Ruth came all the way west, first to Hamburg and then Schleswig-Holstein, while her family remained in the Soviet Zone near Neuruppin, north of Berlin.</p>\n<p>Ruth dropped by unannounced to look at her old family house, but didn't want to knock on the door. The family now there, however, recognized her from her first visit 12 years ago, and invited her in for coffee. The conversation proceeded in a mixture of broken English and German, with the help of an English-Polish dictionary. </p>\n<p>At one point the mother of the family disappeared into the attic. She returned with an old photo for Ruth, a family potrait of Ruth as a young girl with her parents and younger sister. They had found it in the house and had kept it all these years.</p>","fields":{"title":"Tel Aviv","date":"08 Jul 2000","url":"https://papascott.de/2000-07-08-tel-aviv/"}}},"pathContext":{"slug":"/2000-07-08-tel-aviv/","prev":{"fields":{"title":"The Difference between Europe and America","date":"01 Jul 2000","slug":"/2000-07-01-the-difference-between-europe-and-america/"}},"next":{"fields":{"title":"At Your Side","date":"09 Jul 2000","slug":"/2000-07-09-at-your-side/"}}}}

/***/ })

